<template>
  <div class="xtx-checkbox" @click="changeChecked()">
    <i v-if="checked" class="iconfont icon-checked"></i>
    <i v-else class="iconfont icon-unchecked"></i>
    <span v-if="$slots.default"><slot /></span>
  </div>
</template>
<script>
import { useVModel } from "@vueuse/core";
// v-model  ====>  :modelValue  +   @update:modelValue
export default {
  name: "XtxCheckbox",
  props: {
    modelValue: {
      type: Boolean,
      default: false,
    },
  },
  setup(props, { emit }) {
    // useVModelv-model
    // 1. propsmodelValue
    // 2. useVModelpropsmodelValue
    // 3. checked.value
    // 4. checked.value = '' emit('update:modelvalue', '')
    const checked = useVModel(props, "modelValue", emit);
    const changeChecked = () => {
      const newVal = !checked.value;
      // 
      checked.value = newVal;
      // change
      emit("change", newVal);
    };
    return { checked, changeChecked };
  },
};
</script>
<style scoped lang="less">
.xtx-checkbox {
  display: inline-block;
  margin-right: 2px;
  .icon-checked {
    color: var(--xtx-color);
    ~ span {
      color: var(--xtx-color);
    }
  }
  i {
    position: relative;
    top: 1px;
  }
  span {
    margin-left: 2px;
  }
}
</style>
