<template>
  <div class="goods-image">
    <!--  -->
    <div
      class="large"
      :style="[
        {
          backgroundImage: `url(${images[curIndex]})`,
          backgroundPositionX: positionX + 'px',
          backgroundPositionY: positionY + 'px',
        },
      ]"
      v-show="showFlag"
    ></div>
    <div class="middle" ref="target">
      <img :src="images[curIndex]" alt="" />
      <!--  -->
      <div
        class="layer"
        :style="{ left: left + 'px', top: top + 'px' }"
        v-show="showFlag"
      ></div>
    </div>
    <!--  -->
    <ul class="small">
      <li
        v-for="(img, i) in images"
        :key="i"
        @mouseenter="mouseEnterFn(i)"
        :class="{ active: i === curIndex }"
      >
        <img :src="img" alt="" />
      </li>
    </ul>
  </div>
</template>
<script>
import { ref, watch } from "vue";
import { useMouseInElement } from "@vueuse/core";
export default {
  name: "XtxImageView",
  props: {
    images: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  setup() {
    // 
    const curIndex = ref(0);
    function mouseEnterFn(i) {
      curIndex.value = i;
    }

    // 
    const target = ref(null);
    //  false (isOutside bug)
    const showFlag = ref(false);
    // elementX:  refObj
    // elementY: refObj
    // isOutSide:   true  refObj
    const { elementX, elementY, isOutside } = useMouseInElement(target);

    // 
    const left = ref(0);
    const top = ref(0);
    const positionX = ref(0);
    const positionY = ref(0);
    watch([elementX, elementY, isOutside], () => {
      showFlag.value = !isOutside.value;

      // 
      if (isOutside.value) {
        return false;
      }
      //  left top
      // 1. 
      if (elementX.value > 300) {
        left.value = 200;
      }
      if (elementX.value < 100) {
        left.value = 0;
      }
      // 2. 
      if (elementX.value < 300 && elementX.value > 100) {
        left.value = elementX.value - 100;
      }

      if (elementY.value > 300) {
        top.value = 200;
      }
      if (elementY.value < 100) {
        top.value = 0;
      }
      // 2. 
      if (elementY.value < 300 && elementY.value > 100) {
        top.value = elementY.value - 100;
      }

      //  (  )
      // 1. ()
      // 2.  (x2)
      positionX.value = -left.value * 2;
      positionY.value = -top.value * 2;
    });
    /**
     * 1.  
     * 2.    ref  refObj.value
     * 3.  css
     */
    return {
      mouseEnterFn,
      curIndex,
      target,
      elementX,
      elementY,
      left,
      top,
      positionX,
      positionY,
      showFlag,
    };
  },
};
</script>
<style scoped lang="less">
.goods-image {
  width: 480px;
  height: 400px;
  position: relative;
  display: flex;
  .middle {
    width: 400px;
    height: 400px;
    background: #f5f5f5;
  }
  .large {
    position: absolute;
    top: 0;
    left: 412px;
    width: 400px;
    height: 400px;
    z-index: 500;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background-repeat: no-repeat;
    // : = 2:1   background-position
    background-size: 800px 800px;
    background-color: #f8f8f8;
  }
  .layer {
    width: 200px;
    height: 200px;
    background: rgba(0, 0, 0, 0.2);
    //  lefttop
    left: 0;
    top: 0;
    position: absolute;
  }
  .small {
    width: 80px;
    li {
      width: 68px;
      height: 68px;
      margin-left: 12px;
      margin-bottom: 15px;
      cursor: pointer;
      &:hover,
      &.active {
        border: 2px solid var(--xtx-color);
      }
    }
  }
}
</style>
