<template>
  <div class="goods-sku">
    <dl v-for="item in goods.specs" :key="item.id">
      <dt>{{ item.name }}</dt>
      <dd>
        <template v-for="val in item.values" :key="val.name">
          <img
            :class="{ selected: val.selected, disabled: val.disabled }"
            @click="changeSku(item, val)"
            v-if="val.picture"
            :src="val.picture"
            :title="val.name"
          />
          <span
            :class="{ selected: val.selected, disabled: val.disabled }"
            @click="changeSku(item, val)"
            v-else
            >{{ val.name }}</span
          >
        </template>
      </dd>
    </dl>
  </div>
</template>
<script>
import powerSet from "./power-set";
const spliter = "";
// 
const getPathMap = (skus) => {
  // 1. sku  props.goods.skus
  // 2. skusku
  // 3. skupower-set
  // 4.  key-value
  const pathMap = {};
  skus.forEach((sku) => {
    if (sku.inventory > 0) {
      // sku
      // [1,2,3] ==> [[1],[2],[3],[1,2],[1,3],[2,3],[1,2,3]]
      // 
      const valueArr = sku.specs.map((val) => val.valueName);
      //  
      const valueArrPowerSet = powerSet(valueArr);
      // pathMap
      valueArrPowerSet.forEach((arr) => {
        // arrkeykey['',''] ===> ''
        const key = arr.join(spliter);
        // pathMap
        if (pathMap[key]) {
          pathMap[key].push(sku.id);
        } else {
          pathMap[key] = [sku.id];
        }
      });
    }
  });
  return pathMap;
};
// 
const getSelectedValues = (specs) => {
  const arr = [];
  specs.forEach((item) => {
    // 
    const seletedVal = item.values.find((val) => val.selected);
    arr.push(seletedVal ? seletedVal.name : undefined);
  });
  return arr;
};
// 
const updateDisabledStatus = (specs, pathMap) => {
  // 1. disabled
  specs.forEach((item, i) => {
    const selectedValues = getSelectedValues(specs);
    item.values.forEach((val) => {
      // 2. 
      if (val.selected) return;
      // 3. 
      selectedValues[i] = val.name;
      // 4. undefinedkey
      const key = selectedValues.filter((value) => value).join(spliter);
      // 5. keytrue
      val.disabled = !pathMap[key];
    });
  });
};
// 
const initDefaultSelected = (goods, skuId) => {
  // 1. sku
  // 2. sku
  const sku = goods.skus.find((sku) => sku.id === skuId);
  goods.specs.forEach((item, i) => {
    const val = item.values.find((val) => val.name === sku.specs[i].valueName);
    val.selected = true;
  });
};

export default {
  name: "XtxSku",
  props: {
    goods: {
      type: Object,
      default: () => ({ specs: [], skus: [] }),
    },
    skuId: {
      type: String,
      default: "",
    },
  },
  setup(props, { emit }) {
    const pathMap = getPathMap(props.goods.skus);
    // skuId
    if (props.skuId) {
      initDefaultSelected(props.goods, props.skuId);
    }
    // 
    updateDisabledStatus(props.goods.specs, pathMap);
    // 1. selected
    // 1.1 
    // 1.2 
    const changeSku = (item, val) => {
      // 
      if (val.disabled) return;
      // 
      if (val.selected) {
        val.selected = false;
      } else {
        item.values.forEach((valItem) => {
          valItem.selected = false;
        });
        val.selected = true;
      }
      // 
      updateDisabledStatus(props.goods.specs, pathMap);
      // sku{skuId,price,oldPrice,inventory,specsText}
      // 1. sku
      // 2. sku
      const validSelectedValues = getSelectedValues(props.goods.specs).filter(
        (v) => v
      );
      if (validSelectedValues.length === props.goods.specs.length) {
        // 
        const skuIds = pathMap[validSelectedValues.join(spliter)];
        const sku = props.goods.skus.find((sku) => sku.id === skuIds[0]);
        emit("change", {
          skuId: sku.id,
          price: sku.price,
          oldPrice: sku.oldPrice,
          inventory: sku.inventory,
          //  11 ... 
          specsText: sku.specs
            .reduce((p, c) => `${p} ${c.name}${c.valueName}`, "")
            .trim(),
        });
      } else {
        // 
        // 
        emit("change", {});
      }
    };
    return { changeSku };
  },
};
</script>
<style scoped lang="less">
.sku-state-mixin () {
  border: 1px solid #e4e4e4;
  margin-right: 10px;
  cursor: pointer;
  &.selected {
    border-color: var(--xtx-color);
  }
  &.disabled {
    opacity: 0.6;
    border-style: dashed;
    cursor: not-allowed;
  }
}
.goods-sku {
  padding-left: 10px;
  padding-top: 20px;
  dl {
    display: flex;
    padding-bottom: 10px;
    align-items: center;
    dt {
      width: 50px;
      color: #999;
    }
    dd {
      flex: 1;
      color: #666;
      line-height: 40px;
      > img {
        width: 50px;
        height: 50px;
        .sku-state-mixin ();
      }
      > span {
        display: inline-block;
        height: 30px;
        line-height: 28px;
        padding: 0 20px;
        .sku-state-mixin ();
      }
    }
  }
}
</style>
