<script>
import { h } from "vue";
export default {
  name: "XtxBread",
  render() {
    // 
    // 1. template 
    // 2. 
    // 3. vue2.0 hvue3.0 h
    // 4. h        
    // 
    // 1. xtx-bread
    // 2. 
    // 3. xtx-bread-itemirender
    // 4. itemitemi
    // 5. xtx-bread
    const items = this.$slots.default();
    const dymanicItems = [];
    items.forEach((item, i) => {
      // XtxBreadItemTransition
      if (
        item.type.name === "XtxBreadItem" ||
        item.type.displayName === "Transition"
      ) {
        dymanicItems.push(item);
        if (i < items.length - 1) {
          dymanicItems.push(h("i", { class: "iconfont icon-angle-right" }));
        }
      }
    });
    return h("div", { class: "xtx-bread" }, dymanicItems);
  },
};
</script>

